import React, { useEffect, useState, useRef } from 'react'
import './css/reportProblem.css'
import emailjs from 'emailjs-com'
// Icon Library
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

function ReportProblem(props) {
  const [btnTxt, setBtnTxt] = useState('')
  const formRef = useRef(null)
  emailjs.init('o7c3uHOnTzU9CMyHy')

  useEffect(() => {
    formRef.current.addEventListener('submit', function (event) {
      event.preventDefault()

      setBtnTxt('...')

      const serviceID = 'default_service'
      const templateID = 'template_j81x4hm'

      emailjs.sendForm(serviceID, templateID, this).then(
        () => {
          setBtnTxt('')
          formRef.current.reset()
          alert('')
        },
        (err) => {
          setBtnTxt('')
          alert(JSON.stringify(err))
        }
      )
    })
  }, [])

  function problemAgan() {
    setBtnTxt('')
  }
  function closeReport() {}

  return (
    <div id="repProbPage" className={`${props.theme}`}>
      <div
        id="closeRepProb"
        className={`${props.reportProblemActive ? 'open' : ''}`}
        onClick={() => props.setReportProblemActive(false)}>
        <FontAwesomeIcon icon="fa-solid fa-xmark" />
      </div>
      <div
        id="reportProblemPage"
        className={props.reportProblemActive ? 'open' : ''}>
        <form ref={formRef} className="repProbForm">
          <div id="repProbModeView">
            <label htmlFor="repProbMode"></label>
            <select name="repProbMode" id="repProbMode" onChange={problemAgan}>
              <optgroup label="">
                <option></option>
                <option></option>
              </optgroup>
            </select>
          </div>
          <div className="problemtext">
            <span></span>
          </div>
          <div>
            <label htmlFor="repProbType"></label>
            <select
              name="repProbType"
              id="repProbSelect"
              onChange={problemAgan}>
              <optgroup label="">
                <option></option>
              </optgroup>
              <optgroup label="">
                <option></option>
                <option></option>
                <option></option>
                <option></option>
                <option></option>
              </optgroup>
              <optgroup label="">
                <option></option>
                <option></option>
                <option></option>
              </optgroup>
            </select>
          </div>
          <div className="problemtext">
            <span></span>
          </div>
          <div>
            <label htmlFor="repProbArea"></label>
            <select
              name="repProbArea"
              id="repProbSelect"
              onChange={problemAgan}>
              <optgroup label="">
                <option></option>
                <option></option>
              </optgroup>
              <optgroup label="">
                <option></option>
                <option></option>
                <option></option>
                <option></option>
                <option></option>
              </optgroup>
              <optgroup label="">
                <option></option>
                <option></option>
              </optgroup>
            </select>
          </div>
          <div className="problemtext">
            <span></span>
          </div>
          <div id="messgeView">
            <label htmlFor="message"></label>
            <textarea
              name="message"
              id="message"
              placeholder="..."
              onFocus={problemAgan}></textarea>
          </div>
          <div className="repProbBtnView">
            <button className="repProbBtn" type="submit">
              <FontAwesomeIcon icon="fa-solid fa-paper-plane" /> {btnTxt}
            </button>
          </div>
        </form>
      </div>
      <div
        id="repProbBackMask"
        className={props.reportProblemActive ? 'open' : ''}
        onClick={() => props.setReportProblemActive(false)}></div>
    </div>
  )
}

export default ReportProblem
